import { Component, OnInit,ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AppService } from '../services/app.service';
AppService

@Component({
  selector: 'app-match',
  templateUrl: './match.page.html',
  styleUrls: ['./match.page.scss'],
})
export class MatchPage implements OnInit {


  naming_arr = [
    {"en":'aerialsWon',"ar":'التحامات هوائية'},
    {"en":'clearances',"ar":'تخليص'},
    {"en":'corners',"ar":'ضربات ركنيه'},
    {"en":'crosses',"ar":'عرضيات'},
    {"en":'fouls',"ar":'مخالفات ضد'},
    {"en":'goalKicks',"ar":'ركلات المرمى'},
    {"en":'interceptionsMade',"ar":'الاعتراضات'},
    {"en":'longBalls',"ar":'الكرات الطويلة'},
    {"en":'offsides',"ar":'التسلل'},
    {"en":'passes',"ar":'تمريرات'},
    {"en":'passes_completed',"ar":'تمريرات صحيحة'},
    {"en":'possession',"ar":'الإستحواذ'},
    {"en":'saves',"ar":'تصديات'},
    {"en":'shotsBlocked',"ar":'بلوكات'},
    {"en":'shotsOffTarget',"ar":'تسديدات خارج المرمى'},
    {"en":'shots_on_target',"ar":'تسديدات علي المرمى'},
    {"en":'shots_total',"ar":'تسديدات'},
    {"en":'shotsWoodwork',"ar":'تسديدات علي القائم'},
    {"en":'tackles',"ar":'قطع الكرة'},
    {"en":'throwIns',"ar":'رمية تماس'},
    {"en":'touches',"ar":'لمسات'},
    {"en":'yellow_cards',"ar":'بطاقات صفراء'},
    {"en":'yellow_red_cards',"ar":'بطاقات صفراء ادت اللي حمراء'},
    {"en":'red_cards',"ar":'بطاقات حمراء'},
    {"en":'passing_percentage',"ar":' دقة التمرير'}
  ]

  fin_arr = []


  data_loaded = false

  id

  data

  _group
  _match_info
  _team_a_last_five_matches
  _team_b_last_five_matches
  _lastfivematches

  _match_events

  _home_team_id
  _away_team_id
  _home_team_name
  _away_team_name

  _home_lineup
  _away_lineup


  _home_onbench
  _away_onbench

  _match_statistics

  _match_state


  constructor(private _ElementRef:ElementRef,private _ActivatedRoute:ActivatedRoute,private _AppService:AppService) { }


  

  tabs(index){
    this._ElementRef.nativeElement.querySelector('.large-header__tabs li.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelector('.main-tabs-box__tab.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelectorAll('.large-header__tabs li')[index].classList.add('active')
    this._ElementRef.nativeElement.querySelectorAll('.main-tabs-box__tab')[index].classList.add('active')
    this._AppService.scroll_to_top()
  }

  tabs2(index){
    this._ElementRef.nativeElement.querySelector('.modern-tabs__tab.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelector('.modern-tabs__content.active').classList.remove('active')
    this._ElementRef.nativeElement.querySelectorAll('.modern-tabs__tab')[index].classList.add('active')
    this._ElementRef.nativeElement.querySelectorAll('.modern-tabs__content')[index].classList.add('active')
  }

  get_data(_id){
    this._AppService.get_data(`matches/single?id=${_id}`,'s').subscribe((res:any)=>{
      // console.log(res)
      this.data = res.data
      
      this._group = res.data.group
      // console.log(this._group)
      this._team_a_last_five_matches = res.data.teamA_lastFiveMatches
      this._team_b_last_five_matches = res.data.teamB_lastFiveMatches
      this._lastfivematches = res.data.lastfivematches
      this._match_events = res.data.key_event
      this._home_team_id = res.data.teamA.teamId
      this._away_team_id = res.data.teamB.teamId
      this._home_team_name = res.data.teamA.name
      this._away_team_name = res.data.teamB.name
      this._home_lineup = res.data.teamA_lineUp
      this._away_lineup = res.data.teamB_lineUp
      

      this._home_onbench = this.data.teamA_subsOnBench
      this._away_onbench = this.data.teamB_subsOnBench
      // console.log(this._match_statistics)

      this._match_state = this.data.status

      this._match_statistics = res.data.teamStats

      for(let i=0;i<this.naming_arr.length;i++){
        for(let x=0;x<this._match_statistics.length;x++){
          if(this._match_statistics[x].type == this.naming_arr[i].en){
            this.fin_arr.push( this.naming_arr[i].ar)
          }
        }
      }
      // console.log(this.fin_arr)

      // console.log(this._home_lineup)
      this.data_loaded = true
    })
  }

  back(){
    this._AppService.go_back()
  }

  _router(url){
    this._AppService.router_navigation(url)
  }


  ngOnInit() {
    // console.log(this.naming_arr)
    this.id = this._ActivatedRoute.snapshot.params['id'];
    // console.log(this.id)
    this.get_data(this.id)
  }

}
